// import { addDays } from 'date-fns';
import { useState, useEffect } from "react";
import { DateRangePicker } from "react-date-range";
import {connect} from "react-redux"
import moment from 'moment'

import "react-date-range/dist/styles.css"; // main style file
import "react-date-range/dist/theme/default.css"; //css style file


import Modal from 'react-bootstrap/Modal'
import Button from 'react-bootstrap/Button'

import "./MyFormTo.css"
//action
import {startgetFromToData} from "../component/action/fromTo"
import {startShow} from "../component/action/show"


function MyFormTo(props) {
  const [state, setState] = useState([
    {
      startDate: new Date(),
      endDate: new Date(),
      key: "selection",
    },
  ]);
  const [show, setShow] = useState(false);

  const handleClose = () =>{
  props.dispatch(startShow(false))
};
  // const handleShow = () => setShow(true);

  const handleDateChange = (date) => {
    
    let start_date = moment(state[0].startDate).format('DD/MM/YYYY');
    let end_date = moment(state[0].endDate).format('DD/MM/YYYY');


    const totalData = {
      start : start_date,
      end : end_date
    }
    // console.log(totalData,"datesssss")
    const proj=props.projName
    props.dispatch(startgetFromToData(totalData,proj));
    setShow(false)


  };

  // useEffect(() => {
  //   setShow(props.show)
  //   console.log(stat,"stat")
  // }, [])
  // console.log(start_date,"state-myform")
  console.log(props.show,"show")

  return (
    <div className="modal_form">
      {/* <Button variant="primary" onClick={handleShow}>
        Launch demo modal
      </Button> */}
      <Modal show={props.show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Modal heading</Modal.Title>
        </Modal.Header>
        <Modal.Body>
        <DateRangePicker
        onChange={(item) => setState([item.selection])}
        showSelectionPreview={true}
        moveRangeOnFirstSelection={false}
        months={2}
        ranges={state}
        direction="horizontal"
        // onChange={handleDateChange}

      /> 
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
          <Button variant="primary" onClick={handleDateChange}>
            Save Changes
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
}
const mapStateToProps = (state) => {
  return {
    projName: state.projName,
    show: state.show
  };
};
export default connect(mapStateToProps)(MyFormTo);
