const accessPinData = (data) => {
  return { type: "GET_ACCESS_PIN", payload: data };
};

const startAccessPin = (data) => {
  return (dispatch) => {

    const loadData = async () => {
      try {
        console.log(data,"inside date")
        dispatch(accessPinData(data))
        
      } catch (err) {
        console.error(err);
      }
    };
    loadData();
  };
};



export { accessPinData, startAccessPin  };
