  
const fromToInitialState={}

const fromToReducer=(state=fromToInitialState,action)=>{
    switch(action.type){
      case "FROM_TO_DATA":{
          return {...action.payload}
      }
      case "RESET_FROM_TO":{
        return {}
    }
      default:{
          return {...state}
      }
  }
}

export default fromToReducer

