import {combineReducers,createStore,applyMiddleware} from 'redux'
import thunk from 'redux-thunk'

import loggedinReducer from "../reducer/loggedin"
import totalDataReducer from "../reducer/totalData"
import dateDataReducer from "../reducer/dateData"
import projNameReducer from "../reducer/projName"
import fromToReducer from "../reducer/fromTo"
import showReducer from "../reducer/show"
import accessPinReducer from "../reducer/accessPin"

const configureStore=()=>{
    const store=createStore(combineReducers({
      loggedin:loggedinReducer,
      totalData:totalDataReducer,
      dateData: dateDataReducer,
      projName: projNameReducer,
      fromTo: fromToReducer,
      show: showReducer,
      accessPin : accessPinReducer
      
    }),applyMiddleware(thunk))
    return store
}

export default configureStore