
const getShowOn=(login)=>{
    return {type:"SHOW_ON"}
  }
  
  const getShowOff=(login)=>{
    return {type: "SHOW_OFF"}
  }
  
  const startShow=(data)=>{
    return (dispatch)=>{
            const response=data
            if(response){
            dispatch(getShowOn(response))
            }else {
            dispatch(getShowOff(response))
  
            }
    }
  }
  
  export {
    startShow,
    getShowOn,
    getShowOff
  };