import axios from "../../config/axios";
import {getTotalData} from "../action/totalData"

const getFromToData = (tl) => {
  return { type: "FROM_TO_DATA", payload: tl };
};

const resetFromToData = () => {
  return { type: "RESET_FROM_TO" };
};
const startgetFromToData = (date,proj) => {
  return (dispatch) => {

    const loadData = async () => {
      try {
        // console.log(date,"date")
        const data1 = {
          data:date.start
        }
        if (date.start === date.end) {
          const TotalData=await axios.get(`lead_stats?date=${date.start}&project_name=${proj}`)
          dispatch(getTotalData(TotalData.data.data))
        dispatch(getFromToData(data1))

        }else {
        // console.log(`lead_stats_range?from_date=${date.start}&to_date=${date.end}&project_name=${proj}`,"proj")
        const TotalData=await axios.get(`lead_stats_range?from_date=${date.start}&to_date=${date.end}&project_name=${proj}`)
        // console.log(TotalData.data.data,"fse_list_redux")
        dispatch(getTotalData(TotalData.data.data))
        dispatch(getFromToData(date))
        
        }
      } catch (err) {
        console.error(err);
      }
    };
    loadData();
  };
};



export { getFromToData, startgetFromToData ,resetFromToData };
