import "./style.css";
import React, { Component } from "react";
import GoogleLogin from "react-google-login";
import { checkEmail } from "../../utils";
import {startAccessPin} from "../action/accessPin"
import {connect} from "react-redux"

class MyGoogleLogin extends Component {
  responseGoogle = (r1) => {
    // this.props.loginFunc(r);
    // console.log(r1, "Google Response");
    if (r1.accessToken) {
      const email = r1.profileObj.email;
      const fetchCall = async () => {
        const response1 = await fetch(checkEmail, {
          method: "POST",
          headers: {
            "Content-type": "application/json",
          },
          body: JSON.stringify({
            email,
          }),
        });
        if(response1.ok){
          const response=await response1.json();
          // console.log(r1,"r1");
          console.log(response,"response");
          if(response.email_status){
            this.props.loginFunc(r1);
            console.log("login email true");
            this.props.dispatch(startAccessPin(response.userObj.project))
            localStorage.setItem("project", response.userObj.project);
            this.props.setLoginError("");
          }else{
            console.log("login email false");
            this.props.logoutFunc();
            this.props.setLoginError(
              "You don't have access, please contact admin"
            );
          }
          
        }else{
          console.log("login email false");
            this.props.logoutFunc();
            this.props.setLoginError(
              "You don't have access, please contact admin"
            );
        }
      };
      fetchCall();
    } else {
      this.props.logoutFunc();
    }
    // this.props
  // console.log(this.props.dispatch,"r1")
  };

  render() {
    return (
      <>
        <GoogleLogin
          clientId="800470593510-v53lbqn11rmr50tjjk4ah19vqhskutau.apps.googleusercontent.com" //client id
          // clientId="800470593510-v4rijm08vf4iapke1q4jr1k1odg62dp8.apps.googleusercontent.com" //local host
          buttonText="Sign In With Google"
          onSuccess={this.responseGoogle}
          onFailure={this.responseGoogle}
          cookiePolicy={`single_host_origin`}
        />
      </>
    );
  }
}

const mapStateToProps=(state)=>{
  return {
    loggedin:state.loggedin
  }
}

export default connect(mapStateToProps)(MyGoogleLogin)